@import "../../css/shared.scss";
//background-image: url('../../images/ico_02.png');
.p-home{
  .home-wrapper{
    display: flex;
    flex-direction: column;
    height: 100vh;
    overflow: visible;
  }

  .home-banner-polyfill{
    position: absolute;
    width: 100%;
    height: px(705);
    background: url('../../images/home_bg3.png');
    background-size: contain;
  }

  .home-banner{
    width: 100%;
    height: px(508);
    background: url('../../images/home_bg3.png') bottom center no-repeat;
    background-size: 100%;
    position: relative;
    overflow: visible;

    p{
      margin: 0;
      position: absolute;
      bottom: px(26);
      text-align: center;
      width: 100%;
      font-size: px(52);
      text-transform: uppercase;
    }
  }
  .c-card{
    flex-grow: 1;
    flex-shrink: 0;
    margin-top: px(36);
    min-height: px(200);
    padding-bottom: 0;
    display: flex;
    flex-direction: column;

    .home-info{
      margin: 0;
      padding: 0;
      text-transform: uppercase;
      border: 0;
      border-bottom: 2px solid;
      border-image: $linear-gradient 1;
      flex-shrink: 0;
      flex-grow: 0;

      li{
        width: 50%;
        float: left;
        text-align: center;
        margin: 0 0 px(30) 0;
        padding: 0 px(10);
        box-sizing: border-box;

        &:nth-of-type(1){
          strong{
            color: $color-pink;
          }
        }

        &:nth-of-type(2){
          strong{
            color: $color-blue;
          }
        }

        &:nth-of-type(3), &:nth-of-type(4){
          strong{
            color: $color-red;
          }
        }
      }

      strong{
        display: block;
        font-size: px(38);
        line-height: 1.2;
        margin-bottom: px(10);
        font-weight: normal;

      }

      p{
        font-size: px(22);
        line-height: 1.4;
        margin: 0;

        white-space: pre-line;
      }
    }

    .home-info2{
      flex-grow: 1;
      min-height: px(280);
      margin-top: px(30);
      margin-bottom: px(30);
      position: relative;
      background: url('../../images/home_bg2.png') center 30% no-repeat;
      background-size: contain;

      & > div{
        position: absolute;
        font-size: px(36);
        line-height: 1.4;

        &:nth-of-type(1){
          top: 0;
          left: 0;
        }

        &:nth-of-type(2){
          top: 0;
          right: 0;
          text-align: right;

          & > div{
            &:nth-of-type(2){
              height: px(32);
              line-height: px(32);
              padding-right: px(44);
              margin-top: px(10);
              background: url('../../images/icon_member.png') center right no-repeat;
              background-size: contain;
            }
          }
        }

        &:nth-of-type(3){
          bottom: 0;
          left: 0;
        }

        &:nth-of-type(4){
          bottom: 0;
          right: 0;
        }

        &:nth-of-type(3), &:nth-of-type(4){
          strong{
            color: $color-pink;
            font-size: px(48);
            font-weight: normal;
            line-height: px(52);
            margin-right: px(18);
          }

          span{
            line-height: px(52);
            vertical-align: top;
            font-size: px(28);
          }
        }
      }
    }
  }

  .home-bottom{
    height: px(78);
    flex-shrink: 0;
    margin: px(45) px(30) ;
    display: flex;
    align-items: center;
    justify-content: space-around;

    div{
      width: px(30);
      height: 100%;
      flex-shrink: 0;
    }

    button{
      border: 0;
      background: $color-blue;
      height: 100%;
      width: 100%;
      color: #fff;
      flex-grow: 0;
      border-radius: px(7);
      font-size: px(30);
      text-transform: uppercase;

      &:last-of-type{
        background: $color-pink;

        &:active{
          background: $color-pink2;
        }
      }

      &:active{
        background: $color-blue2;
      }

      &:disabled{
        background: rgba(180, 180, 180, .72);
      }
    }
  }

  .home-qrCode-modal{
    text-align: center;

    .p1{
      text-transform: uppercase;
      font-size: px(48);
      margin-top: px(15);
    }

    .home-qrCode{
      width: px(362);
      height: px(362);
      background-image: $linear-gradient;
      border-radius: px(8);
      margin: px(54) auto 0 auto;
      display: flex;
      justify-content: center;
      align-items: center;

      img{
        display: block;
        width: px(330);
        height: px(330);
        border: px(10) solid #fff;
        border-radius: px(8);
        background: #fff;
      }
    }

    .p2{
      padding: 0 px(30);
      word-break: break-all;
      font-size: px(34);
      margin-top: px(54);
    }

    button{
      border: 0;
      background-color: transparent;
      background-image: $linear-gradient2;
      color: #fff;
      min-width: px(246);
      height: px(64);
      border-radius: px(7);
      font-size: px(28);
      text-transform: uppercase;
      line-height: px(54);
      padding: 0 px(24);
      margin-top: px(36);
      margin-bottom: px(36);

      &:active{
        background-image: $linear-gradient;
      }
    }
  }

  .home-recommend-modal{
    .p1{
      text-transform: uppercase;
      font-size: px(34);
      margin-top: px(5);
    }

    .home-input{
      height: px(82);
      margin-top: px(42);
      display: flex;

      div{
        width: px(80);
        height: px(80);
        background: red;
        //flex-grow: 0;
        flex-shrink: 0;
        display: block;
        background: url('../../images/icon_scan.png') center right no-repeat;
        background-size: contain;
      }

      input{
        width: 100%;
        display: block;
        margin-right: px(12);
        background: #2e2e3f;
        border: 0;
        border-radius: px(7);
        line-height: px(82);
        height: px(82);
        padding: 0 px(20);
        font-size: px(30);
        color: #fff;
      }
    }

    button{
      display: block;
      border: 0;
      background-color: transparent;
      background-image: $linear-gradient2;
      color: #fff;
      min-width: px(246);
      height: px(64);
      border-radius: px(7);
      font-size: px(28);
      text-transform: uppercase;
      line-height: px(54);
      padding: 0 px(24);
      margin: px(50) auto px(30) auto;

      &:active{
        background-image: $linear-gradient;
      }
    }
  }
}
